<!-- 服务质量过程监控-业务量 -->
<template>
  <div class="total_traffic">
    <div class="total_traffic_number">
      <div class="title">总业务量</div>
      <div class="title-count">
        <span>{{ ywCountNum }}件</span>
      </div>
      <div
        class="charts-main"
        v-loading="!resYwCharts"
        element-loading-background="rgba(0, 0, 0, 0.2)"
        element-loading-text="加载中"
      >
        <bda-progress-charts
          class="bar-charts"
          v-if="resYwRCharts && barChartsData1.dataMap.length !== 0"
          :barChartsData="barChartsData1"
        ></bda-progress-charts>
        <div class="count">
          <span>已完成{{ repairDone }}件</span>
          <span>抢修类{{ repairTotal }}件</span>
          <span>未完成{{ repairUndone }}件</span>
        </div>
        <bda-progress-charts
          class="bar-charts"
          v-if="resYwRCharts && barChartsData1.dataMap.length !== 0"
          :barChartsData="barChartsData2"
        ></bda-progress-charts>
        <div class="count">
          <span>已完成{{ unrepairDone }}件</span>
          <span>非抢修类{{ unrepairTotal }}件</span>
          <span>未完成{{ unrepairUndone }}件</span>
        </div>
      </div>
      <!-- <bda-area-charts v-if="resYwCharts" :dataInfo="ywData"></bda-area-charts> -->
    </div>
    <div class="total_traffic_ratio">
      <div class="title">总业务量占比</div>
      <div
        class="charts-main"
        v-loading="!resYwRCharts"
        element-loading-background="rgba(0, 0, 0, 0.2)"
        element-loading-text="加载中"
      >
        <bda-pie-charts
          v-if="resYwRCharts"
          :pieChartsData="pieChartsData"
          :roseType="false"
          :labelHide="true"
          :radius="['40%', '80%']"
          :center="['center', '55%']"
          :minAngle="25"
        ></bda-pie-charts>
      </div>
    </div>
  </div>
</template>

<script>
import config from '../../../base/mixin/service-quality-process'
//  import pieCharts from "@/components/pieCharts/pieCharts"
import bdaPieCharts from '../../../widgets/bda-pie-charts/bda-pie-charts'
import bdaProgressCharts from '../../../widgets/bda-progress-charts/bda-progress-charts'

export default {
  name: 'total',
  mixins: [config],
  components: {
    bdaPieCharts,
    bdaProgressCharts,
  },
  props: {},
  data() {
    return {
      // 饼图数据
      pieChartsData: {},
      // 饼图数据是否返回
      resYwRCharts: false,
      //抢修数据
      repairDone: 0,
      repairUndone: 0,
      repairTotal: 0,
      //非抢修数据
      unrepairDone: 0,
      unrepairUndone: 0,
      unrepairTotal: 0,
      // 业务总量总数
      ywCountNum: 0,
      // 业务总量数据
      barChartsData1: { dataMap: [] },
      barChartsData2: {},
      // 刷新业务总量组件
      resYwCharts: false,
      gdTitle: '主城渝西',
      gdCount: 0,
      rightDataMap: {
        抢修类业务量: 0,
        非抢修类业务量: 0,
      },
    }
  },
  watch: {},
  created() {
    //总业务量
    this.getTotalVolume({
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams,
    })
      .then((res) => {
        if (res.length !== 0) this.zywlFilter(res)
      })
      .catch(() => {
        this.resYwCharts = true
      })
    //总业务量占比
    this.totalVolumeForType({
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams,
    })
      .then((res) => {
        if (res.length !== 0) this.zywlRatio(res)
      })
      .catch(() => {
        this.resYwRCharts = true
      })
  },
  mounted() { },

  methods: {
    // 总业务量数据处理
    zywlFilter(data) {
      this.resYwCharts = false
      let mapData = {},
        dataInfo = []
      let typeStatus = {
        '0': '未完成',
        '1': '已完成',
      }
      let types = {
        '0': '非抢修',
        '1': '抢修类',
      }
      if (data && data.length !== 0) {
        for (let item of data) {
          item.name = typeStatus[item.isdone]
          if (mapData[types[item.isrepair]]) {
            mapData[types[item.isrepair]].push(item)
          } else {
            mapData[types[item.isrepair]] = [item]
          }
        }
      } else {
        return
      }
      let allCount = 0
      if (mapData) {
        for (let key in mapData) {
          let _objs = {
            name: key,
            id: Math.random() + '_',
          }
          if (mapData[key]) {
            for (let sys of mapData[key]) {
              allCount += sys.sum
            }
          }
          _objs.children = mapData[key]
          dataInfo.push(_objs)
        }
      }

      let dataMap1 = []
      let dataMap2 = []
      dataInfo[0].children.forEach((e) => {
        dataMap1.push(e.sum)
      })
      dataInfo[1].children.forEach((e) => {
        dataMap2.push(e.sum)
      })
      this.barChartsData1 = {
        dataMap: dataMap1,
        color: ['#00E2FF', '#0074FF'],
      }
      this.barChartsData2 = {
        dataMap: dataMap2,
        color: ['#00FEBB', '#00BDFA'],
      }
      this.ywCountNum = allCount
      window.console.log(this.barChartsData1, 'barChartsData1')
      window.console.log(this.barChartsData2, 'barChartsData2')
      this.repairDone = this.barChartsData1.dataMap[0] || 0
      this.repairUndone = this.barChartsData1.dataMap[1] || 0
      this.repairTotal = (this.barChartsData1.dataMap[0] || 0) + (this.barChartsData1.dataMap[1] || 0)
      this.unrepairDone = this.barChartsData2.dataMap[0] || 0
      this.unrepairUndone = this.barChartsData2.dataMap[1] || 0
      this.unrepairTotal = (this.barChartsData2.dataMap[0] || 0) + (this.barChartsData2.dataMap[1] || 0)

      this.$nextTick(() => {
        this.resYwCharts = true
      })
    },
    //总业务量占比数据处理
    zywlRatio(data) {
      if (data && data.length !== 0) {
        let pieData = data.filter(e => e.ccsum !== 0).map((e) => {
          return {
            value: e.ccsum,
            name: e.name,
          }
        })
        let pieColor = ['#0091fe', '#00c4ff', '#2dfaff', '#49ea8a', '#69fea4', '#a3ff90', '#e2ff7e', '#ffca6a', '#fd8e00', '#cc3426', '#cf4d89', '#c2aac0']
        this.pieChartsData = {
          dataMap: pieData,
          color: pieColor,
        }
      }
      this.$nextTick(() => {
        this.resYwRCharts = true
      })
    },
  },
}
</script>

<style lang="scss" scoped>
.total_traffic {
  width: 100%;
  height: 97%;
  // display: flex;
  // flex-wrap: wrap;
  // justify-content: center;
  // align-items: center;
  .charts-main {
    // height: calc(100% - 22%);
    // box-sizing: border-box;
    width: calc(100% - 40px);
    position: relative;
    padding: 0 20px;
  }
  .bar-charts {
    width: 100%;
    height: 24%;
  }
  .count {
    width: 100%;
    height: 20%;
    display: flex;
    padding: 0 20px;
    box-sizing: border-box;
    span {
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 16px;
      &:nth-child(1) {
        color: #fff;
      }
      &:nth-child(2) {
        color: #60f8a7;
      }
      &:nth-child(3) {
        color: #7eb8fe;
      }
    }
  }
  .total_traffic_number {
    .title-count {
      height: 14%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 20px;
      span {
        color: #48c8f9;
      }
    }
  }
  .total_traffic_number,
  .total_traffic_ratio {
    width: 100%;
    height: calc(50%);
    position: relative;
    .title {
      color: #00e7ff;
      // line-height: 38px;
      font-size: 16px;
      // height: 22%;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  .total_traffic_number {
    background: url("../../../assets/img/监控框1.png") no-repeat top center;
    background-size: 100% 100%;

    .title {
      height: 14%;
      align-items: flex-end;
    }
    .charts-main {
      height: calc(100% - 28%);
    }
  }
  .total_traffic_ratio {
    height: calc(50%);

    background: url("../../../assets/img/监控框2.png") no-repeat top center;
    background-size: 100% 100%;
    .title {
      height: 14%;
      align-items: flex-end;
    }
    .charts-main {
      height: calc(100% - 20%);
    }
  }
}
</style>
