<!--柱状进度条-->
<template>
  <div class="bar-style">
    <div :id="id" class="charts"></div>
  </div>
</template>

<script>
/**
 * @饼状图
 * @author：risheng.huang
 * @参数说明：
 * barChartsData{
 *     dataMap:[name:"",value:''],    /对应的数据值(百分比)
 *     color:['#31fff1','#27ccc1'], //第一个颜色渐变0颜色和百分比数据颜色，第二个渐变1颜色
 * }  
 * */
import echarts from 'echarts';

export default {
  name: "bda-progress-charts",
  props: {
    barChartsData: {
      type: Object,
      default: () => {
        return {}
      }
    },
  },
  data() {
    return {
      id: Math.random() + '_',
      barData: [],
      series: []

    }
  },
  watch: {},
  created() {
  },
  mounted() {
    this.dataFilter(this.barChartsData);
    this.init();
  },
  methods: {
    dataFilter(data) {
      window.console.log(data, 'data');

      //   let that = this
      let series = [];
      let color = [...data.color];
      window.console.log(color, 'data.color');

      if (data.dataMap) {
        this.barData = [...data.dataMap];
        let barData = this.barData
        series = [
          {
            type: 'bar',
            stack: 'chart',
            barWidth: 100,
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(
                  0, 0, 1, 0,
                  [
                    { offset: 0, color: '#019eff', },
                    // { offset: 0.5, color: '#019eff', },                    //柱图渐变色
                    { offset: 1, color: '#00FFD4', },                 //柱图渐变色
                  ]
                )
              }
            },
            data: [barData[0]]
          }, {
            type: 'bar',
            stack: 'chart',
            silent: true,

            itemStyle: {
              normal: {
                color: 'rgba(0, 80, 179,0.3)'
              }
            },
            data: [barData[1]]
          },
        ];
        this.series = series;
      }
    },
    init() {
      let chartsDom = document.getElementById(this.id);
      let charts = echarts.init(chartsDom);
      let options = {
        // grid: [{
        //     containLabel: true
        // }],
        xAxis: [{
          type: 'value',
          max: this.barData[0] + this.barData[1],//all
          show: false,
        }],
        yAxis: [{
          type: 'category',
          show: false,
        }],
        series: this.series
      };


      window.console.log(options, 'options');
      charts.setOption(options)
    }
  }
}
</script>

<style scoped lang="scss">
.bar-style {
  width: 100%;
  height: 100%;
  position: relative;

  .charts {
    width: 100%;
    height: 100%;
  }
}
</style>